import React from 'react'
import Header from './Header'
import { Link } from 'react-router-dom'

const Payment = () => {
    return (
        <>
            <Header />
            <section className="content">
                <div className="page-heading">
                    <h1>Payment History</h1>
                    <ol className="breadcrumb">
                        <li><Link to="/deashboard">Dashboard</Link></li>
                        {/* <li><a href="javascript:void(0);"></a></li> */}
                        <li className="active">Payment</li>
                    </ol>
                </div>
                <div className="panel panel-default">
                    <div className="panel-heading">Amount <i className="fa fa-wallet"> </i> : 1500 Rs</div>
                    <div className="panel-body">
                        <div className="table-responsive">
                            <table className="table">
                                <thead>
                                    <tr>
                                        <th>S.No.</th>
                                        <th>Type</th>
                                        <th>Amount</th>
                                        <th>Time</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr>
                                        <td>1</td>
                                        <td>
                                            <label className="label label-danger">Debit</label>
                                        </td>
                                        <td>250 Rs</td>
                                        <td><time dateTime="2023-02-14 20:00">2023-04-14 20:00</time></td>
                                    </tr>
                                    <tr>
                                        <td>2</td>
                                        <td>
                                            <label className="label label-warning">Credit</label>
                                        </td>
                                        <td>1000 Rs</td>
                                        <td><time dateTime="2023-02-14 20:00">2023-04-15 21:00</time></td>
                                    </tr>
                                    <tr>
                                        <td>3</td>
                                        <td>
                                            <label className="label label-danger">Debit</label>
                                        </td>
                                        <td>250 Rs</td>
                                        <td><time dateTime="2023-02-14 20:00">2023-04-14 20:00</time></td>
                                    </tr>
                                    <tr>
                                        <td>4</td>
                                        <td>
                                            <label className="label label-warning">Credit</label>
                                        </td>
                                        <td>1000 Rs</td>
                                        <td><time dateTime="2023-02-14 20:00">2023-04-15 21:00</time></td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </section>


        </>
    )
}

export default Payment